"use client";
import React, { useEffect } from "react";
import { Segment } from "../types"; // ‚Üê „É´„Éº„É¨„ÉÉ„Éà„ÅÆÂûã„ÇíË™≠„ÅøËæº„ÇÄ

// üí¨ PropsÂûãÂÆöÁæ©ÔºöApp.tsx„ÅßÊ∏°„Åó„Å¶„ÇãÂÜÖÂÆπ„Å´Âêà„Çè„Åõ„Å¶‰øÆÊ≠£
interface WinnerModalProps {
  winner: Segment;      // ÂΩìÈÅ∏„Åó„Åü„Çª„Ç∞„É°„É≥„ÉàÔºà‰æãÔºö„É©„Éô„É´„ÇÑËâ≤„Å™„Å©Ôºâ
  onClose: () => void;  // „É¢„Éº„ÉÄ„É´„ÇíÈñâ„Åò„ÇãÈñ¢Êï∞
}

const WinnerModal: React.FC<WinnerModalProps> = ({ winner, onClose }) => {
  // üîÑ Escape„Ç≠„Éº„ÅßÈñâ„Åò„Çâ„Çå„Çã„Çà„ÅÜ„Å´„Åô„Çã
  useEffect(() => {
    const handleKeyDown = (event: KeyboardEvent) => {
      if (event.key === "Escape") {
        onClose();
      }
    };
    window.addEventListener("keydown", handleKeyDown);
    return () => window.removeEventListener("keydown", handleKeyDown);
  }, [onClose]);

  // winner„ÅåÂ≠òÂú®„Åó„Å™„ÅÑÊôÇ„ÅØ‰Ωï„ÇÇË°®Á§∫„Åó„Å™„ÅÑ
  if (!winner) return null;

  return (
    <div
      className="fixed inset-0 bg-black/60 flex items-center justify-center z-50 p-4 backdrop-blur-sm"
      onClick={onClose}
      aria-modal="true"
      role="dialog"
    >
      {/* „É¢„Éº„ÉÄ„É´Êú¨‰Ωì */}
      <div
        className="bg-white rounded-2xl shadow-2xl p-6 sm:p-8 w-full max-w-md text-center relative transform animate-scale-in"
        onClick={(e) => e.stopPropagation()} // „É¢„Éº„ÉÄ„É´Â§ñ„ÇØ„É™„ÉÉ„ÇØ„ÅßÈñâ„Åò„Çã
      >
        <h2 className="text-2xl sm:text-3xl font-bold text-gray-800 mb-4">üéØ ÁµêÊûúÁô∫Ë°® üéØ</h2>

        {/* üé° ÂΩìÈÅ∏„Çª„Ç∞„É°„É≥„ÉàÂêç„ÇíË°®Á§∫ */}
        <p className="text-lg sm:text-xl text-gray-700 mb-6 break-words leading-relaxed">
          <span className="font-semibold text-emerald-600">{winner.label}</span> „ÅåÂΩì„Åü„Çä„Åæ„Åó„ÅüÔºÅ
        </p>

        <button
          onClick={onClose}
          className="mt-2 w-full bg-gradient-to-r from-green-500 to-emerald-600 text-white font-bold py-3 rounded-lg text-lg shadow-lg hover:from-green-600 hover:to-emerald-700 focus:outline-none focus:ring-4 focus:ring-green-300 transition-all duration-300 transform hover:scale-105"
        >
          Èñâ„Åò„Çã
        </button>

        {/* ‚ú®„Ç¢„Éã„É°„Éº„Ç∑„Éß„É≥ÂäπÊûú */}
        <style>{`
          @keyframes scale-in {
            from { opacity: 0; transform: scale(0.9); }
            to { opacity: 1; transform: scale(1); }
          }
          .animate-scale-in {
            animation: scale-in 0.3s ease-out forwards;
          }
        `}</style>
      </div>
    </div>
  );
};

export default WinnerModal;
